<template>
  <div>
    <div class="page-register">
      <div class="header">
        <header>
          <router-link :to="{name:'index'}">
            <a href="#" class="site-logo"></a>
          </router-link>
          <div class="login">
            <span>&nbsp;&nbsp;</span>
            <router-link :to="{name:'login'}"></router-link>
          </div>
        </header>
      </div>
      <!-- header -->
      <div class="content">
        <el-form
          :model="registerForm"
          status-icon
          :rules="rules"
          ref="registerForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="userName">
            <el-input type="text" placeholder="4-16" v-model="registerForm.userName" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input
              type="password"
              v-model="registerForm.password"
              autocomplete="off"
              @input="input"
              placeholder="6-16"
            ></el-input>
          </el-form-item>
          <div class="pw-strength">
            <!-- - -->
            <div :class="['bar',strengthClass]"></div>
            <div class="letter">
              <span class="week"></span>
              <span class="normal"></span>
              <span class="strong"></span>
            </div>
          </div>
          <el-form-item label="" prop="rePassword">
            <el-input type="password" v-model="registerForm.rePassword" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('registerForm')"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <!-- form -->
      <footer>
        <p>
          <a href="#"> meituan.com  ICP070791  11010502025545</a>
        </p>
      </footer>
      <!-- footer -->
    </div>
  </div>
</template>

<script>
import api from "@/api/index.js";
export default {
  data() {
    // 
    var validateUser = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value.length < 4 || value.length > 16) {
        callback(new Error("4-16"));
      } else {
        callback();
      }
    };
    // 
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value.length < 6 || value.length > 16) {
        callback(new Error("6-16"));
      } else {
        callback();
      }
    };
    // 
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.registerForm.password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      registerForm: {
        userName: "",
        password: "",
        rePassword: "",
        rules: {}
      },
      strengthClass: "", // 
      rules: {
        // 
        userName: [{ validator: validateUser, trigger: "blur" }],
        password: [{ validator: validatePass, trigger: "blur" }],
        rePassword: [{ validator: validatePass2, trigger: "blur" }]
      }
    };
  },
  methods: {
    submitForm(formName) {
      // 
      // console.log(this.registerForm.password,this.registerForm.rePassword)
      this.$refs[formName].validate(valid => {
        if (valid) {
          // ,
          api
            .register({
              params: {
                userName: this.registerForm.userName,
                password: this.registerForm.password,
                rePassword: this.registerForm.rePassword
              }
            })
            .then(res => {
              if (res.data.status == "success") {
                alert("~");
                this.$router.push({ name: "login" });
              } else {
                alert(res.data.msg);
              }
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    input() {
      // password  
      var regStr = /(\w)+/g;
      var regNum = /(\d)+/g;
      var reg = /_/g;
      var strongth =
        this.registerForm.password.match(reg) &&
        this.registerForm.password.match(regStr) &&
        this.registerForm.password.match(regNum);
      // ( ||  || )
      if (
        this.registerForm.password.length > 20 ||
        (this.registerForm.password.length > 6 && strongth)
      ) {
        this.strengthClass = "strong";
      } else if (this.registerForm.password.length < 6) {
        this.strengthClass = "week";
      } else if (!this.registerForm.password) {
        this.strengthClass = "";
      } else {
        this.strengthClass = "normal";
      }
    }
  }
};
</script>

<style lang="scss">
@import "@/assets/css/register/index.scss";
</style>
