<template>
  <div>
    <span class="name"></span>
    <!-- select -->
    <m-select
      :list="provinceList"
      title=""
      :value="province"
      :showWrapperActive="provinceActive"
      @change_active="changeProvinceActive"
      @change="changeProvince"
      className="province"
    />
    <m-select
      :list="cityList"
      title=""
      :value="city"
      :disabled="cityDisabled"
      :showWrapperActive="cityActive"
      @change_active="changeCityActive"
      @change="changeCity"
      className="city"
    />
    <!--  search -->
    <span></span>
    <el-select
      v-model="searchWord"
      filterable
      remote
      reserve-keyword
      placeholder=""
      :remote-method="remoteMethod"
      :loading="loading"
    >
      <el-option v-for="item in searchList"
      :key="item"
      :label="item"
      :value="item"></el-option>
    </el-select>
  </div>
</template>

<script>
import api from '@/api/index.js'
import MSelect from './select.vue'
export default {
  created () {
    api.getProvinceList().then(res => {
      this.provinceList = res.data.data.map(item => {
        item.name = item.provinceName
        return item// name
      })
    })
  },
  data () {
    return {
      cityDisabled: true,
      provinceList: [], // list
      province: '', // value
      cityList: [],
      city: '', // value
      // ()
      cityActive: false,
      provinceActive: false,
      searchList: [], // 
      searchWord: '',
      loading: false // loadingflagajaxtruefalse
    }
  },
  components: {
    MSelect
  },
  methods: {
    changeProvinceActive (flag) { // provinceflag
      this.provinceActive = flag
      if (flag) {
        this.cityActive = false
      }
    },
    changeCityActive (flag) { // cityflag
      this.cityActive = flag
      if (flag) {
        this.provinceActive = false
      }
    },
    // /
    changeProvince (item) { // 
      this.province = item.name
      this.cityDisabled = false// flag
      this.cityList = item.cityInfoList
    },
    changeCity (item) { // 
      this.city = item.name
      this.$store.dispatch('setPosition', item)// actions(mutations)login
      // geo.vue
      this.$router.push({ name: 'index' })// /index
    },
    remoteMethod (val) { // ,val
      // console.log(val);
      // 
      api.getProvinceList().then(res => {
        // console.log(this.provinceList)
        res.data.data.filter(item => {
          // return item.provinceName.indexOf(value) > -1; //> -1 
          return item.provinceName.indexOf(val) > -1 ? this.searchList.push(item.provinceName) : ''
        })
      })
    }
  }
}
</script>

<style lang="scss">
@import "@/assets/css/changeCity/iselect.scss";
</style>
