import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const state = {
    position: {},
    userName:''
}
const mutations = {
    setPosition(state, val) {
        state.position = val;//categroy.vuechangeCity       
    },
    setUserName(state,val){
        state.userName = val;
    }
}
const actions = {
    setPosition({ commit }, val) { //statecommit
        //
        commit("setPosition", val)//Mutations

        // window.navigator.geolocation.getCurrentPosition(position => {
        //      //
        //     commit(position, val)//Mutations
        // }, () => {
        //     console.log("");
        //     commit("setPosition", val)//Mutations
        // })
    }
}
export default new Vuex.Store({
    state,
    mutations,
    actions
})









