<template>
  <div class="search-panel">
    <el-row class="m-header-searchbar">
      <el-col :span="3" class="left">
        <!-- logo -->
        <router-link :to="{name:'index'}">
          <img src="http://s0.meituan.net/bs/fe-web-meituan/fa5f0f0/img/logo.png" alt="" />
        </router-link>
      </el-col>
      <el-col :span="15" class="center">
        <div class="wrapper">
          <!--  -->
          <form>
            <el-input
              v-model="searchWord"
              placeholder=","
              @focus="focus"
              @blur="blur(2000)"
              @input="input"
              v-on:keyup.enter="submit"
            ></el-input>

            <el-button type="primary" icon="el-icon-search"></el-button>
          </form>
          <dl class="hotPlace" v-if="isHotPlace">
            <!-- focus -->
            <dt></dt>
            <dd v-for="(item,index) in hotPlaceList.slice(0,5)" :key="index">
              <router-link :to="{name : 'goods',params :{ name:item }}"></router-link>
              <router-link :to="{name : 'goods',params :{ name:item }}">{{item}}</router-link>
            </dd>
          </dl>
          <dl class="searchList" v-if="isSearchList">
            <!--  -->
            <dd v-for="(item,index) in searchList" :key="index">
              <router-link :to="{name : 'goods',params :{ name:item }}">{{item}}</router-link>
            </dd>
          </dl>
        </div>
        <p class="suggest">
          <router-link
            v-for="(item,index) in suggestList"
            :to="{name : 'goods',params :{ name:item }}"
            :key="index"
          >{{item}}</router-link>
        </p>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import api from '@/api/index.js'
export default {
  data () {
    return {
      searchWord: '',
      isFocus: false,
      hotPlaceList: [], // 
      searchList: ['', '', ' '],
      suggestList: []
    }
  },
  created () {
    api.getSearchHotWord().then(res => {
      this.hotPlaceList = res.data.data
      this.suggestList = res.data.data
    })
  },
  computed: {
    isHotPlace () {
      return this.isFocus && !this.searchWord
    },
    isSearchList () {
      return this.isFocus && this.searchWord
    }
  },
  methods: {
    focus () {
      this.isFocus = true
    },
    blur (delay) {//
      const self = this
      setTimeout(() => {
        self.isFocus = false // 200
      }, delay)
    },
    input () {
      // 
      const value = this.searchWord
      api.getSearchList().then(res => {
        this.searchList = res.data.data.list.filter(item => {
          return item.indexOf(value) > -1 // > -1 
        })
      })
    }
  }
}
</script>
